.life-bar {
  position: relative;
  left: 5%;
  top:5px;
  width: 265px;
  max-height: 30px;
  border: 3px solid #000;
  border-radius: 15px;
  overflow:visible;
  background-color: rgb(75, 0, 0);
  transition: box-shadow 0.3s ease;

}
.life-bar-animation{

  animation: box-shadow-animation 4s infinite;
}
.life-bar-poisonAnimation{

  animation: box-shadow-poisonAnimation 4s infinite;
}
.life-bar-burnAnimation{
  animation: flicker 1s infinite alternate
}

@keyframes box-shadow-animation {
  0% {
    box-shadow: 0px 0px 30px 5px #b60000;
  }
  50% {
    box-shadow: 0px 0px 5px 5px #b60000;
  }
  100% {
    box-shadow: 0px 0px 30px 5px #b60000;
  }
}
@keyframes box-shadow-poisonAnimation {
  0% {
    box-shadow: 0px 0px 30px 2px #7fb600;
  }
  50% {
    box-shadow: 0px 0px 5px 5px #15b600;
  }
  100% {
    box-shadow: 0px 0px 30px 2px #95b600;
  }
}
@keyframes flicker {
  0% {
    box-shadow: 0 0 5px 2px #f00, 0 0 10px 5px #f00, 0 0 20px 10px #f00;
  }
  100% {
    box-shadow: 0 0 5px 2px #f00, 0 0 15px 5px #f00, 0 0 30px 10px #f00;
  }
}


.life-bar-fill {
  position:absolute;
  height: 90%;
  background-color: red;
  transition: width 0.5s ease-in-out;
  border-radius: 15px;
  align-items: center;
  justify-content: center;
